<template>
    <div class="container">
        <h2 class="page-header">Install</h2>
        <p>use <strong>npm</strong> to install plugin</p>
        <pre class="brush:bash">
            npm i v-page --save
        </pre>
        <p>Include plugin in your <code>main.js</code> file</p>
        <pre class="brush:js">
            import Vue from 'vue'
            import vPage from 'v-page';
            Vue.use(vPage);
        </pre>

        <h2 class="page-header">Deploy on your component</h2>
        <p>template code</p>
        <script type="syntaxhighlighter" class="brush:js">
        <![CDATA[
            <template>
              <!-- v-bind 'setting' data to config page bar -->
              <!-- bind event 'page-change' to receive page info change -->
              <v-page :setting="pageSet" @page-change="pageChange"></v-page>
            </template>
        ]]>
        </script>

        <p>script code</p>
        <pre class="brush:js">
export default {
    data(){
        return {
            pageSet: {
                totalRow: 0,//required option
                language: 'en',//default: 'cn'
                pageSizeMenu: [20,100]//default: [10, 20, 50, 100]
            }
        }
    },
    methods:{
        //receive page info change callback
        pageChange(pInfo){
            let that = this;
            console.log(pInfo);//{pageNumber: 1, pageSize: 10}
            //for example, do some http request, get new data and update totalRow property
            axios({
            ...
            }).then(function(resp){
                that.totalRow = resp.totalRow;
            });
        }
    }
};
        </pre>

        <h2 class="page-header">Example</h2>
        <v-page :setting="page" @page-change="pageChange"></v-page>
        <div class="row text-center">
            <h3>page change logs</h3>
            <div v-for="log in pageMsg">{{log}}</div>
        </div>

        <h3>Gallery demo <small><a href="https://codepen.io/terry05/pen/yjZYLR" target="_blank">view on CodePen</a></small></h3>
        <div class="photo-wall">
            <div v-for="num in pageArr" v-html="num" class="brick"></div>
        </div>
        <v-page :setting="pagePhoto" @page-change="pagePhotoChange"></v-page><br>

        <h3>no page length menu</h3><br>
        <v-page :setting="noLengthMenu"></v-page><br>

        <h3>no page info bar</h3><br>
        <v-page :setting="noInfo"></v-page><br>

        <h3>mimi mode, no info bar, no page length</h3><br>
        <v-page :setting="mini"></v-page><br>


        <h2 class="page-header">Options</h2>
        <table class="table table-bordered table-striped">
            <thead>
                <tr>
                    <th>name</th>
                    <th>type</th>
                    <th>description</th>
                    <th>required</th>
                    <th>default</th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td>totalRow</td>
                    <td>number</td>
                    <td>the total number of records in query<br>in parent component, you need update totalRow number when it's change</td>
                    <td>true</td>
                    <td>0</td>
                </tr>
                <tr>
                    <td>info</td>
                    <td>boolean</td>
                    <td>set to show page info bar or no</td>
                    <td>false</td>
                    <td>true</td>
                </tr>
                <tr>
                    <td>language</td>
                    <td>string</td>
                    <td>
                        the page bar language
                        <ul>
                            <li><code>'cn'</code></li>
                            <li><code>'en'</code></li>
                            <li><code>'jp'</code></li>
                        </ul>
                    </td>
                    <td>false</td>
                    <td>'cn'</td>
                </tr>
                <tr>
                    <td>pageSizeMenu</td>
                    <td>Array|boolean</td>
                    <td>page length menu list, set <code>false</code> to close page length menu</td>
                    <td>false</td>
                    <td>[10, 20, 50, 100]</td>
                </tr>
                <tr>
                    <td>align</td>
                    <td>string</td>
                    <td>
                        the page bar alignment direction
                        <ul>
                            <li><code>'left'</code></li>
                            <li><code>'center'</code></li>
                            <li><code>'right'</code></li>
                        </ul>
                    </td>
                    <td>false</td>
                    <td>'right'</td>
                </tr>
            </tbody>
        </table>

        <h2 class="page-header">Events</h2>
        <table class="table table-bordered table-striped">
            <thead>
            <tr>
                <th>name</th>
                <th>description</th>
                <th>optionalPayload</th>
            </tr>
            </thead>
            <tbody>
            <tr>
                <td>page-change</td>
                <td>when page change or page length change, plugin will emit an event to the parent</td>
                <td>
                    <ul>
                        <li><code>pageNumber</code><br>current page number</li>
                        <li><code>pageSize</code><br>the total number of records in one page</li>
                    </ul>
                </td>
            </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
    export default {
        data(){
            let arr = [];
            for (let i = 0; i < 108; i++) {
                arr.push(i+1);
            }
            return {
                page: {
                    totalRow: 21,
                    language: 'en',
                    align: 'center'
                },
                pagePhoto: {
                    totalRow: arr.length,
                    language: 'en',
                    align: 'center'
                },
                noInfo: {
                    totalRow: 100,
                    align: 'left',
                    language: 'en',
                    info: false
                },
                noLengthMenu: {
                    totalRow: 100,
                    align: 'left',
                    language: 'en',
                    pageSizeMenu: false
                },
                mini:{
                    totalRow: 100,
                    align: 'left',
                    info: false,
                    language: 'en',
                    pageSizeMenu: false
                },
                pageMsg: [],
                arr: arr,
                pageArr: []
            };
        },
        methods: {
            pageChange(pInfo){
                this.pageMsg.push(pInfo);
            },
            pagePhotoChange(pInfo){
                this.pageArr.splice(0, this.pageArr.length);
                let start=0, end=0;
                start = pInfo.pageSize * (pInfo.pageNumber-1);
                end = start + pInfo.pageSize;
                if(end > this.arr.length) end = this.arr.length;
                for (let i = start; i < end; i++) {
                    this.pageArr.push(this.arr[i]);
                }
            }
        },
        mounted(){
            SyntaxHighlighter.highlight();
        }
    }
</script>

<style lang="scss" scoped>
    div.photo-wall {
        div.brick {
            border: 1px solid #DDDDDD;
            border-radius: 2px;
            background-color: #F4F4F4;
            text-align: center;
            line-height: 100%;
            float: left;
            margin: 0 10px 10px 0;
            width: 180px;
            height: 130px;
            font-size: 60px;
            color: #BBBBBB;
            padding-top: 35px;
        }
    }
</style>


